* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

input {
  font-size: 1.2rem;
  color: gray;
  width: 100%;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  margin: 0;
  padding: 5px;
  max-width: 200px;
  text-align: center;
}

.card img {
  max-height: 170px;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

@media (max-width: 500px) {
  
  body {
    padding: 5px;
  }

  .card {
    max-height: 120px
  }

  .card img{
    max-width: 135px;
    max-height: 100px;
  }

  .card p {
    padding: 0px;
    margin: auto;
    font-size: 8px;
    max-width: 100px;
    text-align: center;
  }

  ol {
    margin: 0;
    padding: 0;
  }


}